import React from "react";
import '../styles.css'
import flutter from '../images/Cutie_Fluttershy.jpg'
import grumpy from '../images/grumpycat.jpg'


//click on image
export const Secret1 = () => {
    document.getElementById("cv_picture").style.backgroundImage = `url(${flutter})`
    document.getElementById("cv_picture").style.backgroundSize = "Contain";
    if (document.getElementById("fluttershy") === null) // only works once, wont add unlimited elements by clicking
    {
      document.getElementById("cv_text").insertAdjacentHTML('beforebegin', `<p id="fluttershy">"Fluttershy is Kindness, <br/> And or all that is Cute and <b>FLUFFY</b>!"</p>`) 
    }
}


//Typeing secret : fluffy
function ModalSecret({ setOpenModal }) {
  return (
    <div className="modalBackground" onClick={() => {
        setOpenModal(false);
      }}>
      <div className="modalContainer">
        <div className="titleCloseBtn">
          <button
            onClick={() => {
              setOpenModal(false);
            }}
          >
            X
          </button>
        </div>
        <div className="title">
        <h4>Aha! Some more hidden stuff!</h4>
        </div>
        <div className="body">
        <p>
            <img src={grumpy} alt="" id="grumpyCat"/>
        </p>
        </div>
        <div className="footer">

        <h2>When you're grumpy about frontend development</h2>
        </div>
      </div>
    </div>
  );
}

export default ModalSecret;