import React from "react"
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/esm/Col";

export default function Profile(props) {
  return (
    <>
    <Col>
    <Card style={{ width: '18rem' }}>
      <Card.Img variant="top" src={props.owner.avatar_url} alt={props.owner.login}/>
      <Card.Body className="bg-dark">
        <Card.Title>{props.owner.login}</Card.Title>
        <Card.Text>
        {props.name}
        </Card.Text>
        <Card.Link href={props.html_url} target="_blank" rel="noreferrer">View Repo</Card.Link>
        <Card.Text>language: {props.language}</Card.Text>
      </Card.Body>
    </Card>
              </Col>
    </>
  )
}