import React from "react"
import PostData from '../data/jsonStuff.json'
import cvpic from '../images/cv_pic2.jpg'
import { Secret1 } from './Secrets.js'

export const CvComponent = () => {
    return (
<div>
  <span className="grid-container" lang="se">
    <div className="grid-item">
      <div id="cv_picture">
        <label htmlFor="cv_pic" onClick={Secret1}>
          <img src={cvpic} alt="" />
        </label>        
      </div>
      <div id="cv_text">        
        <h2>
          Programmerings språk
        </h2>       
        <p>
          C ,C++ ,C# , Mel / Pymel ,Python 
          Java,Javascript/jquery,PHP, CSS, HTML, SQL, VBA.
        </p>
        <h2>
          Tidigare / pågående utbildningar
        </h2>
        <div id="education">
          {/* Collected from Json */}
            {PostData.map((postDetail,index) => {
            return <p key = {index}>
                {postDetail.educate}
            </p>
            })}
        </div>
        <h2>
          Tidigare arbetsplatser
        </h2> 
        <div id="workplace">
          {/* Collected from Json */}
          {PostData.map((postDetail2,index) => {
            return <p key={index}>
                
                {postDetail2.workplace}
                
            </p>
            })}
        </div>
        <h2>
          Språk Skriftligt/Muntligt
        </h2> 
        <p>
          Svenska (modersmål)<br />
          Engelska (flytande)<br />
        </p>
        <h2>
          Erfarenheter:
        </h2>
        <p>
          -Support av datorer samt kringutrustning (nätverk, mobiler, skrivare), inkluderar felsökning/underhåll/konstruktion från delar till fungerande datorer.<br />
          -Hantering av dokumentation och programmering inom Office paketet 365 (excel vba)<br />
          -hantering av virtuella maskiner inom Azure miljö<br />
          -Catering inför bröllop &amp; Event<br />
        </p>
        <h3>
          Kontakt:
        </h3>
        <p>
          Phone: +46738250435<br/>
         Adress: Florsjö 240 82693 Söderhamn<br/>
          Email: Daniel.svensson682@gmail.com<br/>
        </p>
      </div>
    </div>
  </span>
</div>


            )
            
}